.dialogs{
    display:grid;
    grid-template-columns:2fr 10fr ;
    height: 100%;
    
}
.dialogsItems{
    padding: 10px;
    font-size: 25px;
    border: 1px solid;
    
    
}

.dialog{
    text-decoration: none;
    color: #000;
    height: 50px;
}

.messages{
    padding: 10px;
    border: 1px solid;
    position: relative;
    
}
.message{
    background-color: #069cf3;
    border-radius: 30px;
    width: 50%;
    margin-bottom: 5px;
    padding: 5px 10px;
}

.dialog a.activeLink {
    color: #4b90a7;
    text-decoration: none;
    background-color: rgb(215, 248, 248);
}
.send{
    position: absolute;
    bottom: 10px;
    width: 85%;
}
.inputArea{
    width: 100%;
    height: 60px;
}